.clock {
  align-items: center;
  background-color: gold;
  border: 10px solid goldenrod;
  border-radius: 80%;
  display: flex;
  height: 14rem;
  justify-content: center;
  margin: 4rem auto;
  width: 14rem;
}

.clock-text {
  color: rgb(88, 47, 12);
  font-size: 2.25rem;
  font-weight: 800;
}
